

// Contact Form & GOOGLE MAP
// 


.contact-form{
	label{
		color: $black;
		margin-bottom: 10px;
		font-size: 14px;
		font-weight: 600;
	}

	.form-control{
		border:0px;
		font-size: 14px;
		background: $secondary-color;
	}
}
.form-control:hover,.form-control:focus ,.form-control.focus{
	border-color:$primary-color;
	box-shadow: $primary-color;
}


#map{
	min-height:680px;
}

.contact-map{
	position: relative;
	padding: 80px;

	&:before{
		position: absolute;
		content:"";
		right: 0px;
		top:0px;
		width: 90%;
		height: 100%;
		background: $secondary-color;
	}
	#map{
		position: absolute;
		content:"";
		width: 50%;
		height: 80%;
		left: -50px;
		top:10%;
	}
}