// CTA
// 

.cta-wrap{
	padding: 60px;
	margin-top: -90px;
	background: $white;
	position: relative;
	border-radius: 5px;
	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height: 100%;
		background: url("../images/4.png") no-repeat;
		background-size: cover;
		opacity: .5;
	}
}


//experience
//

.experience{
	.content{
		padding: 20px 40px 20px 0px;
		border-bottom:1px solid #eee;

		h5{
			margin-bottom: 10px;
		}
		p{
			margin-top: 20px;
		}
	}
}




//  Team
//  
#team{
	padding-bottom: 200px;
	img{
		width: 100%;
	}
}

.team-block{
	position: relative;
	z-index: 2;
	border:10px solid $white;
	box-shadow: 0px 16px 32px 0px rgba(167, 178, 219, 0.3);

	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height: 70%;
		background:$primary-color;
		z-index: 1;
		opacity: 0;
		visibility: hidden;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	.team-content{
		padding-left:40px;
	}

	.overlay-content{
		padding-left: 50px;
		position: absolute;
		content:"";
		left: 0px;
		right: 0px;
		width: 100%;
		height: 100%;
		margin: auto;
		top:30%;
		z-index: 3;
		margin-top: -50px;
		opacity: 0;
		visibility: hidden;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;

		h4,span ,a i{
			color: $white;
		}
		p{
			color: $white;
			opacity: .8;
		}

		ul{
			margin-top: 30px;
		}

		ul li a{
			margin: 15px 10px 0px 0px;
		}
	}


	&:hover:before{
		opacity: 1;
		visibility: visible;
		height: 100%;
	}
	&:hover .overlay-content{
		opacity: 1;
		visibility: visible;
	}

}




#testimonial{
	position: relative;
	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height: 70%;
		background: $secondary-color;
	}



	.slick-dots {
	    bottom: -60px;
	    text-align: right;
	}

	.slick-dots li{
		margin: 0px;
	}
	.slick-dots li button::before{
		font-size: 12px;
	}	

	.slick-dots li.slick-active button::before{
		color: $primary-color;
	}
}

.testimonial-item {
	background: $white;
	padding: 40px;
	margin-bottom: 10px;
	border-radius: 10px;
	position: relative;
	box-shadow: 0px 4px 8px 0px rgba(209, 210, 226, 0.3);

	.testimonial-image{
		img{
			width: 320px;
			border-radius: 100%;
		}
	}

	.testimonial-content{
		padding-right: 50px;
	}
	i{
		font-size: 40px;
		position: absolute;
		left: 39px;
		top: 0px;
		font-size: 80px;
		opacity: .09;
	}
}




// Skill


.lh-45{
	line-height: 45px;
}


.skill-bar{
	padding: 20px 10px 0px 10px;
	h4{
		color: $base-color;
	}
}

.progress{
	height: 5px;
	width: 100%;
	overflow: visible;
	position: relative;
	margin: 0 0 35px 0;
	border-radius: 0px;
}

.progress-bar {
    position: absolute;
    width: 0;
    top: 0;
    left: 0;
    height: 100%;
    background: $primary-color;
    border-radius: 0px;
    transition: all 3s ease-in-out;
}

.progress-bar .percent-text {
    font-size: 32px;
	color: $black;
	font-weight: 600;
	position: absolute;
	top: -62px;
	left: 0;
}


//  POrtfolio
//  
.portfolio-item{
	position: relative;
	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height: 70%;
		background:$primary-color;
		z-index: 1;
		opacity: 0;
		visibility: hidden;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;
	}

	.overlay-content{
		position: absolute;
		content:"";
		left: 0px;
		right: 0px;
		width: 100%;
		height: 100%;
		margin: auto;
		top:50%;
		text-align: center;
		z-index: 3;
		margin-top: -25px;
		opacity: 0;
		visibility: hidden;
		-webkit-transition: all .45s ease;
		   -moz-transition: all .45s ease;
		    -ms-transition: all .45s ease;
		     -o-transition: all .45s ease;
		        transition: all .45s ease;


	       h5{
	       	color: $white;
	       	margin-bottom: 20px;
	       }

	       a{
		       	width: 40px;
		       	height: 40px;
		       	background: $secondary-color;
		       	color: $primary-color;
		       	text-align: center;
		       	display: inline-block;
		       	padding-top: 8px;
		       	border-radius: 100%;

		       	&:hover{
		       		outline: none;
		       		text-decoration: none;
		       	}
	       }
	}

	&:hover:before{
		opacity: 1;
		visibility: visible;
		height: 100%;
	}
	&:hover .overlay-content{
		opacity: 1;
		visibility: visible;
	}
}

.border-bottom{
	border-bottom:1px solid rgba(0,0,0,0.07) !important;
}



// Portfolio
// 
.portfolio{
  .btn:focus,.btn:hover{
    box-shadow: none!important;
  }
  .btn.active{
    background: rgba(255, 61, 79, 0.04);
    background: transparent;
    color: $primary-color;
  }
  .btn-group > .btn{
    box-shadow: none!important;
  }

  .btn-group:not(:first-child) > .btn, .btn-group > .btn:not(:first-child) {
    border-top-left-radius: 0px;
    border-bottom-left-radius:0px;
  }
}


