// Fonts 

@import url('https://fonts.googleapis.com/css?family=Literata:400,600,700&display=swap|Open Sans');
body {
  line-height: 1.7;
  font-family: $secondary-font;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  color: $base-color;
  overflow-x: hidden;
}
h1,.h1,h2,.h2,h3,.h3,h4,.h4,h5,.h5,h6,.h6 {
  font-family: $primary-font; 
  font-weight:700;
  color: $black;
}

h1 {
  font-size:44px;
  line-height: 1.3;
}
h2 {
  font-size: 32px;
  line-height: 1.3;
}
h3 {
  font-size: 28px;
  line-height: 1.3;
}
h4 {
  font-size:22px;
  line-height: 1.4;
}

h5 {
  font-size:18px;
  line-height: 1.4;
}
h6 {
  font-size:16px;
  line-height: 1.4;
}


h1 a,h2 a,h3 a,h4 a ,h5 a,h6 a {
  color: inherit;
}

.text-sm{
  font-size: 14px;
}

.text-color{
  color: $primary-color;;
}


.letter-spacing{
  letter-spacing: 1px;
}
