
// Button Style

.btn {
  display: inline-block;
  font-size: 14px;
  letter-spacing: 0px;
  padding: 14px 35px;
  text-transform:uppercase;
  font-weight: 700;
  border-radius: 3px;
  border:2px solid transparent;
  transition: all 0.3s ease 0s;
  border-radius:0px;
  box-shadow: 0 11px 22px rgba(34,34,34,.2);
}


.btn-main {
  background: $primary-color;
  color: $white;
  display: inline-block;
  font-size: 14px;
  text-transform: uppercase;
  @include transition (all, 0.2s, ease);

  &:hover{
    background: #000a2d;
    border-color:#000a2d;
    color: $white;
  }
}


.btn-secondary{
  background: #000a2d!important;
}

.btn-rounded{
  border-radius: 28px;
}


.btn.active:focus, .btn:active:focus, .btn:focus {
  outline: 0;
  box-shadow: none;
}


.bg-shadow {
  box-shadow: 0px 0px 82px -20px rgba(0, 0, 0, 0.33);
}

.bg-gray {
  background:#f9f9f9;
}

.bg-dark {
  background:#202122;
}


.section {
  padding:100px 0;
}
.section-sm {
  padding:70px 0;
}


.section-top{
  padding-top: 100px;
}
.section-bottom{
  padding-bottom: 100px;
}


.section-title {
  margin-bottom: 70px;

  h2{
    position: relative;
    padding-bottom: 20px;

    &:before {
        position: absolute;
        content: "";
        left: 0px;
        bottom: 0px;
        width: 40px;
        height: 3px;
        right: 0px;
        margin: 0 auto;
        background: $primary-color;
    }
  }
}

.title{
   margin-bottom: 70px;

  h2{
    position: relative;
    padding-bottom: 20px;

    &:before {
        position: absolute;
        content: "";
        left: 0px;
        bottom: 0px;
        width: 40px;
        height: 3px;
        background: $primary-color;
    }
  }
}


.overly {
  position: relative;
  &:before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #1d192c;
    opacity: 0.8;
  }
}


a{
  color: $black;
  -webkit-transition: all .25s ease;
       -moz-transition: all .25s ease;
        -ms-transition: all .25s ease;
         -o-transition: all .25s ease;
            transition: all .25s ease;
  &:hover{
    color: $primary-color;
  }
}

a:focus,a:hover{
  outline: none;
  text-decoration: none;
}

.secondary-bg{
  background: $secondary-color;
}
.primary-bg{
  background: $primary-color;
}

.form-control{
  height: 53px;
  border-radius: 0px;
  padding-left: 20px;
}

.form-group{
  margin-bottom: 20px;
}