.about-wrap{
	position: relative;
	margin-top: -60px;
	background: $white;
	padding: 40px;
	border-radius: 5px;
}


.text-lg{
	font-size: 55px;
	line-height: 1.2;
}

// Service
// 


.service-item{
	padding: 60px 40px;
	border:2px solid $secondary-color;
	background: $white;
	position: relative;
	z-index: 1;
	-moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;

	h4,i,p{
		-moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;
	}
	i{
		font-size: 50px;
		color: $primary-color;
	}
	p{
		margin-bottom: 50px;
	}

	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		background: $primary-color;
		width: 100%;
		height: 70%;
		opacity: 0;
		z-index: -1;
		-webkit-transition: all .45s ease;
	   -moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;
	}

	&:hover:before{
		opacity: 1;
		height: 100%;
	}

	&:hover{
		h4{
			color: $white;
		}
		p{
			color: $white;
		}
		.read-more{
			color: $white;
		}
		i{
			color: $white;
		}
	}

	.read-more{
		color: $base-color;
	}

}

.read-more{
	text-transform: uppercase;
	text-decoration: underline;
	font-size: 14px;
	font-weight: 600;
	letter-spacing:.5px;
	-webkit-transition: all .45s ease;
	   -moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;
	i{
		font-size: 14px;
		margin-left: 5px;
		-webkit-transition: all .45s ease;
	   -moz-transition: all .45s ease;
	    -ms-transition: all .45s ease;
	     -o-transition: all .45s ease;
	        transition: all .45s ease;
	}

	&:hover i{
		margin-left: 8px;
	}
}

// Intro content
// 


.intro-img{
	position: relative;

	&:before{
		position: absolute;
		content:"";
		left: -40px;
		top:-30px;
		background: $secondary-color;
		width: 420px;
		height: 420px;
		z-index: -1;
	}
	img{
		width: 100%;
	}
}
.intro-content{
	h2{
		position: relative;
		padding-top: 30px;
		&:before{
			position: absolute;
			content:"";
			left: 0px;
			top:0px;
			background: $primary-color;
			width:50px;
			height: 5px;
			z-index: -1;
		}
	}
	ul li{
		margin-bottom: 20px;
		i{
			color: $base-color;
		}
	}
}


// Counter 
// 


.counter-item{
	text-align: center;
	padding: 40px;
	 background: rgb(246,245,255);
	background: -webkit-linear-gradient(bottom, rgba(246,245,255,1) 0%, rgba(255,255,255,1) 100%);
	background: -o-linear-gradient(bottom, rgba(246,245,255,1) 0%, rgba(255,255,255,1) 100%);
	background: linear-gradient(to top, rgba(246,245,255,1) 0%, rgba(255,255,255,1) 100%); 
	i{
		font-size: 40px;
		color: $primary-color;
	}
	.counter{
		font-size: 50px;
		line-height: 1.1;
		display: block;
		font-weight: 700;
		margin: 15px 0px 15px 0px;
		font-family: $primary-font;
		color: $black;
	}
	h5{
		text-transform: capitalize;
		color:#6e7797;
	}
}

@include mobile{
	.about-wrap {
	    margin-top: 55px;
	}

	.text-lg {
	    font-size: 45px;
	}
}
@include mobile-xs{
	.about-wrap {
	    margin-top: 55px;
	}

	.text-lg {
	    font-size: 45px;
	}
}

@include tablet{
	.about-wrap {
	    margin-top: 55px;
	}

	.text-lg {
	    font-size: 45px;
	}
}

