
//  Blog Sidebar
//  
.blog-bg{
	background: #fafafa;
}

					
.sidebar{
	.widget{
		padding: 0px 20px 40px 30px;
		background: $white;
		overflow:hidden;
		border:2px solid $secondary-color;
	}

	.widget-title{
		position: relative;	
		padding-left: 30px;
		padding-top: 20px;
		margin-bottom: 40px;
		overflow:hidden;

		&:after{
			background: $primary-color;
		    display: block;
		    content: '';
		    position: absolute;
		    left: 0;
		    top: 0;
		    width: 6px;
		    height: 100%;
		    margin: 0;
		}
	}
	.recent-posts{
		li{
			margin-bottom: 30px;
		}
	}
	.img-thumb{
		img{
			width: 230px;
		}
		margin-right: 15px;
	}

	.social-links{
		a{
			width: 50px;
			height: 50px;
			background:rgb(243, 243, 243);
			text-align: center;
			border-radius: 100%;
			display: inline-block;
			padding-top: 14px;

			&:hover{
				background: $primary-color;
				color: $white;
			}
		}
		.list-inline-item:not(:last-child){
			margin-right: 0px;
		}

	}


	.tags{
		a{
			display: inline-block;
			padding: 5px 10px;
			margin-bottom: 5px;
			background: $secondary-color;
		}
	}
}


.widget-list-item{
	ul{
		li{
			padding: 15px 0px;
			border-bottom:1px solid rgba(0,0,0,0.05);

			&:last-child{
				border-bottom:0px;
			}
		}
		
	}
}




// Post Single

.post-title{
	margin-bottom: 25px;
}
.post-tags{
	a{
		display: inline-block;
		padding: 4px 15px;
		border:2px solid $secondary-color;
		color: $base-color;
	}
}

.single-post-meta{
	margin-bottom: 30px;
	li{
		margin-right: 15px!important;
		i{
			margin-right: 10px;
		}
	}
}

//  Custom Pagination

.custom-pagination{
	margin: 60px 0px;

	h6{
		margin-bottom: 15px;
	}

	.older{
		width: 49%;
		float: left;
		padding: 30px;
		border:10px solid $secondary-color;
		margin-right: 3px;
	}

	.newer{
		text-align: right;
		border:10px solid $secondary-color;
		padding: 30px;
	}
}

.post-sub-heading{
	margin-bottom: 35px;
}


.comments-form{
	border:15px solid $secondary-color;
	padding: 30px;

	.form-control{
		background: $secondary-color;
		border-color: transparent;
	}
}

.author-block{
	background: $secondary-color;
	padding: 40px;
	margin-top: 50px;

	img{
		border-radius: 100%;
	}
}

// Comments


.comments-list{
	padding: 30px;

	img{
		border-radius:5px;
		margin-right: 25px;
	}
	li{
		margin-bottom: 20px;
	}

	.media-body {
		.media{
			margin-top: 30px;
		}
	}

	h6{
		margin-bottom: 0px;
	}
	p{
		font-size: 14px;
		margin-top: 10px;
	}

	.post-time{
		font-size: 14px;
	}

	.comment-button{
		float: right;
		color: $base-color;
	}
}


.quote-post{
	margin: 20px 0px;
	padding: 30px 40px;
	font-size: 22px;
	font-family: $primary-font;
	border-left:3px solid $base-color;
}

pre {
   background: $secondary-color;
   padding: 20px 0px;
}

.post-footer{
	margin-top: 40px;
	border-top:1px solid rgba(0,0,0,0.06);
	padding-top: 20px;
}


@include mobile{
	.custom-pagination .older {
	    width: 100%;
	}
}
@include mobile-xs{
	.custom-pagination .older {
	    width: 100%;
	}
}
@include tablet{
	.custom-pagination .older {
	    width: 100%;
	    margin-bottom: 10px;
	}
}