.slider{
	position: relative;
	padding: 140px 0px ;
	// background: #eee;
	background: url("../images/banner-img.jpg") no-repeat;
	background-size: cover;

	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 100%;
		height: 100%;
		background: #000a2d;
		opacity: .9;
	}

	
}

.banner-content{
	z-index: 2;
	h1{
		font-size: 85px;
		line-height: 1.1;
		margin-bottom: 25px;
		color: $white;
	}

	
	.btn{
		margin-top: 20px;
	}
}



.socials-icon a {
    font-size: 20px;
    margin-right: 20px;
    color: $white;
}

.box-shadow	{
		box-shadow: 0px 4px 8px 0px rgba(209, 210, 226, 0.3);
}

.cd-headline.clip b.is-visible{
	text-decoration: underline;
}

//  Banner-2


.banner{
	background: #000a2d;
	background: url("../images/main-banner1.jpg") 80% 50%;
	background-size: cover;
	position: relative;
	min-height:700px;
	overflow:hidden;

	.banner-content{
		padding-top: 100px;
		z-index: 2;
		h1{
			font-size: 80px;
			line-height: 1.1;
			margin-bottom: 25px;
		}

		
		.btn{
			margin-top: 20px;
		}
	}


}

.banner-img{
	position: absolute;
	content:"";
	bottom:-90px;
	right: 60px;
	height: 100%;
}


// Resposnive
// 


@include mobile{
	.slider h1 {
	    font-size: 35px;
	    line-height: 1.1;
	}

	.banner .banner-content h1 {
	    font-size: 35px;
	    line-height: 1.1;
	}

	.banner-content{
		padding: 70px 0px;
	}
	.lead {
	    font-size: 1rem;
	}
	.slider::before{
		display: none;
	}
	.banner{
		min-height:400px;
	}

}
