.footer{
	background: #000a2d;
	background: url("../images/footer-bg.png") no-repeat;
	background-size: cover;
	padding: 270px 0px 70px 0px;
}

.footer-widget.widget{
	h6{
		font-family: $secondary-font;
		text-transform: uppercase;
		color: $base-color;
	}
	h5{
		color: $white;
		margin-bottom: 15px;
	}

	.footer-socials-icon{
		a {
			color: $white;
			font-size: 20px;
			margin-right: 15px;
		}
	}
}



.footer-top{
	a{
		width: 60px;
		height: 60px;
		background: $primary-color;
		display: inline-block;
		color: $white;
		text-align: center;
		padding-top: 15px;
		position: fixed;
		bottom: 30px;
		right: 40px;
	}
}

.footer-btm{
	margin-top: 80px;
	padding-top: 20px;
	border-top:1px solid #19244b;

	p{
		font-size: 18px;
	}
}



.footer-subscribe{
		position: relative;

	.form-control{
		background: transparent;
		border:1px solid #19244b;
		height: 45px;
		font-size: 14px;
	}

	i{
		position: absolute;
		right: 20px;
		top: 30%;
		font-size: 18px;
	}
}












@include mobile{
	.footer{
		background: #000a2d;
		padding-top: 120px;
	}
}
@include mobile-xs{
	.footer{
		background: #000a2d;
		padding-top: 120px;
	}
}
@include tablet{
	.footer{
		background: #000a2d;
		padding-top: 120px;
	}
}
@include desktop{
	.footer{
		background: #000a2d;
		padding-top: 120px;
	}
}


